<template>
  <div id="hello">
      <div class="wrap">
          <div class="message">
              <h2>个人博客</h2>
              <ul class="list">
                <li v-focus:test="loginname">
                  <b>账号：</b>
                  <input type="text" v-model.lazy='loginname.val'>
                  <p v-if='loginname.state'>{{loginname.msg}}</p>
                </li>
                 <li v-focus:required='loginpw'>
                  <b>密码：</b>
                  <input type="password" v-model.lazy='loginpw.val' @blur='fn(loginpw)'>
                  <p v-if='loginpw.state'>{{loginpw.msg}}</p>
                </li>
                <li>
                  <button @click='login'>登录</button>
                </li>
              </ul>

          </div>
      </div>
  </div>
</template>

<script>
 export default {
        // name: 'login',
        data() {
            return {
                loginname: {
                    val: "",
                    msgread: "您输入的姓名不符合中文格式",
                    msg: "您输入的姓名不符合中文格式",
                    test: /^[a-zA-z]+$/,
                    state: false
                },
                loginpw: {
                    val: "",
                    msgread: "您输入的密码格式不对",
                    blur: false,
                    msg: "您输入的密码格式不对",
                    test: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    state: false
                }
            }
        },
        methods: {
            login() {
                // 判断是否值是否正确
                var _this = this
                if (!this.loginpw.state && !this.loginname.state && this.loginpw.val != "" && this.loginname.val != "") {
                    this.axios.post("/api/back_class", {
                        loginname: this.loginname.val,
                        loginpw: this.loginpw.val
                    }).then(function(data) {
                        console.log(data)
                        _this.$message(data.data.msg);
                        if (data.data.code == "1001") {
                            sessionStorage.setItem("userId", data.data.userid);
                            sessionStorage.setItem("username", _this.loginname.val);
                            _this.$router.push("/main")
                        }
                    })
                } else {
                    alert("请正确填写用户名密码")
                }

            },
            // 必填项的验证
            fn(val) {
                val.blur = true
            }
        },
        directives: {
            focus: {
                componentUpdated(el, option, vnode, oldVnode) {
                    var $testState = option.value.test.test(option.value.val)
                    if (option.value.val != "") {
                        if ($testState) {
                            option.value.state = false
                        } else {
                            option.value.state = true
                        }
                        option.value.msg = option.oldValue.msgread
                    } else {
                        if (option.arg == "required" && option.value.blur) {
                            option.value.state = true
                            option.value.msg = "不能为空"
                        } else {
                            option.value.state = false
                        }

                    }
                }

            }

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #hello{
    width:100%;
    height: 100%;
    background: url(../../static/img/2.jpg) no-repeat;
  }
  .wrap{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .message{
    width: 400px;
    height:300px;
   border:2px solid #9F0A0C;
   border-radius: 10px;
   position: absolute;
   top:0;
   left:0;
   right:0;
   bottom:0;
     margin: auto;
     padding:10px 10px;
  }
  .message h2{
    font-size: 16px;
    color: #9F0A0C;
    height: 40px;
    line-height: 40px;
  }
  .list{
    width: 100%;
  }
  .list li{
    width: 100%;

    height: 60px;
    line-height: 30px;
   text-align: center;
   margin-top: 10px;

  }
  .list li b{
    font-size: 14px;
    color: #9F0A0C;
  }
  .list li input{
    height: 30px;
    width: 200px;
    border:1px solid #9F0A0C;
    border-radius: 5px;
    outline: none;
    text-indent: 10px;
  }
  .list li p{
    font-size: 12px;
    color: #9F0A0C;
    text-align: center;
  }
  .list li button{
    width: 30%;
    height: 35px;
    color: #fff;
    outline: none;
    border:0;
    background: #9F0A0C;
  }
</style>



