<template>
	<div>
	<el-table
    :data="everypage"
    stripe
     class='tab'>
    <el-table-column
      prop="title"
      label="标题"
      width="200">
    </el-table-column>
    <el-table-column
      prop="backorfont"
      label="前后端分离"
      width="200">
    </el-table-column>
    <el-table-column
      prop="time"
      label="时间" width='200'>
    </el-table-column>
    <el-table-column

      label="操作" width='200'>
      <template scope='scope'>
      	<el-button type="primary" icon="edit" @click="update(scope.$index,scope.row)"></el-button>
      	<el-button type="primary" icon="delete" @click='less(scope.$index,scope.row)'></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="block">

    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[5, 10, 15, 20]" :page-size="5" layout="total, sizes, prev, pager, next, jumper" :total="alldata.length">
    </el-pagination>
</div>
<el-dialog title="提示" :visible.sync="dialogVisible" size="tiny">
    <span>确定删除此接口文档</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="deleteFn">确 定</el-button>
  </span>
</el-dialog>
	</div>
</template>
<script>
	export default{
		data() {
            return {
                itemId: "",
                dialogVisible: false,
                currentPage4: 1,
                alldata: [],
                everypagelist: 5,
                everypage: []
            }
        },
        mounted() {
          var that=this;
            this.axios.get("/api/apilist/list?type=back").then(function(data) {
                that.alldata = data.data.data
                // console.log(that.alldata)
               for(var i=0;i<that.alldata.length;i++){

                that.everypage.push({
                  title:that.alldata[i].title,
                  backorfont:that.alldata[i].backorfont,
                  time:that.alldata[i].time,
                  id:that.alldata[i].id
                })
                 // console.log( that.everypage)
               }
            }.bind(this))
        },
        methods:{
          tableRowClassName(row, index) {
                if (index === 1) {
                    return 'info-row';
                } else if (index === 3) {
                    return 'positive-row';
                }
                return '';
            },
            handleSizeChange(val) {
                this.everypagelist = val

            },
            handleCurrentChange(val) {

                this.everypage = this.alldata.slice(this.everypagelist * (val - 1), this.everypagelist * val)

            },
          update(ind,rows){
            console.log(ind,rows)
            this.$router.push({
                    name: "update",
                    params: rows
                })

          },
          less(index,row){
            console.log(row)
            this.itemId=row.id;
             this.dialogVisible = true
          },
          deleteFn() {
                this.axios.post("/api/apilist/delete", {
                    id: this.itemId
                }).then(function(data) {
                    console.log(data.data)
                    if (data.data.code == "2032") {
                        this.dialogVisible = false
                        this.open2()
                        this.$router.go(0)
                    } else {
                        this.dialogVisible = false
                        this.open4()
                    }
                    console.log(this.alldata)
                }.bind(this))
            },
             open2() {
                this.$message({
                    message: '恭喜你，删除消息成功',
                    type: 'success'
                });
            },
            open4() {
                this.$message.error('不好意思，删除失败');
            }
        }
	}
</script>
<style>
  .tab{
    background: skyblue;
  }
</style>


