<template>
   <div style="width:700px">
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <h3>添加文章</h3>
    <el-form-item label="文章名字" prop="title">
        <el-input type="text" v-model="ruleForm2.article_name" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="一级类名" prop="url">
        <el-select v-model="ruleForm2.oneId" placeholder="请选择" @change='changeOne()'>
        <el-option
          v-for="item in oneClass"
          :key="item.id"
          :label="item.enname"
          :value="item.id">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="二级类名" prop="backorfont">
      <el-select v-model="ruleForm2.twoId" placeholder="请选择">
        <el-option
          v-for="item in twoClass"
          :key="item.id"
          :label="item.enname"
          :value="item.id">
        </el-option>
      </el-select>
    </el-form-item>
<el-form-item label="是否显示" prop="type">
  <template>
  <el-radio class="radio" v-model="ruleForm2.art_show" label="1">备选项</el-radio>
  <el-radio class="radio" v-model="ruleForm2.art_show" label="0">备选项</el-radio>
  </template>
</el-form-item>
<el-form-item label="是否推荐" prop="type2">
  <template>
  <el-radio class="radio" v-model="ruleForm2.recommend" label="1">备选项</el-radio>
  <el-radio class="radio" v-model="ruleForm2.recommend" label="0">备选项</el-radio>
  </template>
</el-form-item>
<el-form-item label="作者" prop="sendparams" >
    <el-input type="text" v-model="ruleForm2 .editer" auto-complete="off"></el-input>
</el-form-item>
<el-form-item label="时间" prop='getTime'>
    <div class="block">
    <span class="demonstration">默认</span>
    <el-date-picker
      v-model="timedata"
      type="datetime"
      placeholder="选择日期时间" @change='fn' format='yy-MM-dd HH:mm:ss'>
    </el-date-picker>
  </div>
</el-form-item>
<el-form-item label="内容" prop="getparams">
      <div class="components-container">
   <!--  <div class="info">UE编辑器示例<br>需要使用编辑器时，调用UE公共组件即可。可设置填充内容defaultMsg，配置信息config(宽度和高度等)，可调用组件中获取内容的方法。</div> -->

    <div class="editor-container">
      <UE :defaultMsg=ruleForm2.content :config=config ref="ue"></UE>
    </div>
  </div>
</el-form-item>

<el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
    <el-button @click="resetForm(ruleForm2)">重置</el-button>
</el-form-item>
</el-form>

  </div>
</template>
<style>
  .info{
    border-radius: 10px;
    line-height: 20px;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
  }
</style>
<script>
  import UE from './public.vue';
  export default {
    components: {UE},
    data() {
      var checkTime=(rule,value,callback)=>{
          if(this.timedata===''){
              callback(new Error('请输入时间'))
          }else{
            callback();
          }
      }
      return {
        defaultMsg: '这里是UE测试',
        config: {
          initialFrameWidth: 800,
          initialFrameHeight:300
        },
        data:null,
        oneClass:[],
        twoClass:[],
        twoClassAll:[],
        value1:'',
        arr:[],
        timedata:'',
        ruleForm2: {
            article_name: "",
            oneId: "",
            twoId: "",
            editer: "",
            time: "",
            content: "",
            daodu:'',
            imgsrc:'',
            recommend:'0',
            art_show:'0',
            enname_one:''
        },
                rules2: {
                    title: [{
                        required: false,
                        message: '请输入文章名称',
                        trigger: 'blur'
                    }],
                    url: [{
                        required: false,
                        message: '请输入一级类名',
                        trigger: 'blur'
                    }],
                    backorfont: [{
                        required: false,
                        message: '请选择二级类名',
                        trigger: 'blur'
                    }],
                    type: [{
                        required: false,
                        message: '请选择是否显示',
                        trigger: 'blur'
                    }],
                     type2: [{
                        required: false,
                        message: '请选择是否推荐',
                        trigger: 'blur'
                    }],
                    sendparams: [{
                        required: false,
                        message: '请选择是否',
                        trigger: 'blur'
                    }],
                    getparams: [{
                        required: false,
                        message: '请输入内容',
                        trigger: 'blur'
                    }],
                     getTime: [{
                        required: false,
                        message: '请输入时间',
                        trigger: 'blur',
                        validator:checkTime
                    }]
                }
      }
    },
    created(){
      var that=this;
      this.axios.get("/api/back_article/getClass").then(function(data){
          if(data.data.code=='3001'){
             that.oneClass=data.data.data[0];
             that.twoClassAll=data.data.data[1];
             // console.log(that.oneClass)
          }
      })
  // console.log(that.oneClass)

    },
    mounted(){

    },
    methods: {

      fn(){
        this.ruleForm2.time = arguments[0]+0;
         this.ruleForm2.time=this.ruleForm2.time.slice(0,-1)



      },
       submitForm(formName) {
        var that=this;
            this.ruleForm2.content=this.$refs.ue.getUEContent();
             // this.ruleForm2.time =  this.timedata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                     console.log(this.ruleForm2)
                      this.axios.post('api/back_article/addArticle',this.ruleForm2).then(function(data){
                        if(data.data.code=='3011'){
                            that.open2();
                        }else{
                          that.open4();
                        }
                      })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
       },
      resetForm(formName) {
          this.$refs[formName].resetFields();
      },
      changeOne(){
        // this.twoClass=[];
        var that=this,
            onedata=null;
        this.oneClass.forEach(function(i){

          if(i.id==that.ruleForm2.oneId){
            onedata=i;
          }
        })

        console.log(onedata)
         this.ruleForm2.enname_one=onedata.enname;
        this.twoClassAll.forEach(function(v,i){
            if(v.parent_id==that.ruleForm2.oneId){
              // console.log(v)
              that.twoClass.push(v)
            }
        })


      },
      open2() {
          this.$message({
              message: '插入数据成功'
          });
      },
      open4() {
          this.$message.error('插入数据失败');
      }
    }
  };
</script>


