<template>
	<div>
		<el-select v-model="value" placeholder="请选择" class='select' @change='change($event)'>
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
  <el-form-item label="中文" prop="pass">
    <el-input type="text" v-model="ruleForm2.pass" auto-complete="off"></el-input>
  </el-form-item>
  <el-form-item label="英文" prop="checkPass">
    <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
    <el-button @click="resetForm('ruleForm2')">重置</el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<style>

</style>
<script>
  export default {

    data() {
      var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('年龄不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('中文类名不能为空'));
        }else if(/^[\u4e00-\u9fa5_a-zA-Z0-9]+$/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的中文类名'));

        }
      };
      var validatePass2 = (rule, value, callback) => {
         if (value === '') {
          callback(new Error('请输入英文标识'));
        } else if(/[a-zA-Z]/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的英文标注'));
        }
      };
      return {
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        ruleForm2: {
          pass: '',
          checkPass: '',
          age: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      }
    },
    created(){
      var that=this;
    	this.axios.get('/api/back_class/select_one_class').then(function(data){
    		console.log(data.data)
        if(data.data.code=="1021"){
          that.options=[];
          data.data.data.forEach(function(i){

            that.options.push({value:i.id,label:i.enname})
            console.log( that.options)
          })
        }

    	})
    },
    methods: {
      change(e){
        this.id=e;
      },
      submitForm(formName) {
        var that=this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
               this.axios.post("api/back_class/add_two_class",{
               oneId:that.id,
               enname_two:that.ruleForm2.pass,
               cnname_two:that.ruleForm2.checkPass
            }).then(function(data){
              console.log(data)
              that.$message({
                message:data.data.msg,
                // type: 'success'
              });

            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>


