<template>
	<div>
       <el-table
    :data="everypage"
    style="width: 600px">
    <el-table-column type="expand">
      <template scope="props" class='tab'>
          <el-table
          :data="props.row.twodata"
          style="width: 600px">

               <el-table-column
                label="二级中文"
                prop="enname">
              </el-table-column>
              <el-table-column
                label="二级标识"
                prop="cnname">
              </el-table-column>
              <el-table-column
                  label="操作"
                 >
                 <template scope='scope'>
                    <el-button type="primary" icon="edit" @click='update2(scope.$index,scope.row)'></el-button>
                        <el-button type="primary" icon="delete" @click='delate2(scope.$index,scope.row,props.row.onedata)'></el-button>
                 </template>
              </el-table-column>

         </el-table>

          <!--  -->
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="一级中文"
      prop="onedata.enname">
    </el-table-column>
    <el-table-column
      label="一级标识"
      prop="onedata.cnname">
    </el-table-column>
    <el-table-column
      label="操作"
     >
     <template scope='scope'>
        <el-button type="primary" icon="edit" @click='update1(scope.$index,scope.row)'></el-button>
            <el-button type="primary" icon="delete" @click='delate1(scope.$index,scope.row
            )'></el-button>
     </template>
    </el-table-column>
  </el-table>
<el-dialog title="提示" :visible.sync="dialogVisible" size="tiny">
    <span>确定删除此接口文档</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="deleteFn()">确 定</el-button>
  </span>
</el-dialog>
<el-dialog title="提示" :visible.sync="dialogVisible2" size="tiny">
    <span>确定删除此接口文档</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="deleteFn2()">确 定</el-button>
  </span>
</el-dialog>
	</div>
</template>
<style>
  demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    text-align: center;


  }
  .demo-table-expand{
     display: flex;flex:1;
  }
  .tab{
    display: flex;
  }
  .single{
    flex:1;
    text-align: center;
  }

</style>
<script>
  export default{
        data() {
                return {
             everypage:null,
             itemId:null,
             dialogVisible: false,
              dialogVisible2: false,
             name:'',
             id2:null
        }


        },
        mounted() {
          var that=this;
            this.axios.get("/api/back_class/class_list").then(function(data){
              // console.log(data.data.data)

                that.everypage=data.data.data;

              })
            console.log(that.everypage)
            },
        methods:{
          update1(ind,row){
            console.log(ind,row)

            this.$router.push({name:'amond1',params:row})
          },
          update2(index,rows){

            console.log(index,rows)
            this.$router.push({name:'amond2',params:rows})
          },
          delate1(ind,row){
            console.log(row)
            this.itemId=row.onedata.id;
             this.dialogVisible = true
             this.name=row.onedata.enname;
          },
          delate2(index,rows,onedata){
            console.log(onedata)
            this.id2=rows.id;
            this.itemId=rows.parent_id;
             this.dialogVisible2 = true;
             this.name=onedata.enname;
          },
          deleteFn() {
            var that=this;
                this.axios.post("/api/back_class/delete_one", {
                    id: that.itemId,
                    enname_one:that.name
                }).then(function(data) {
                    // console.log(data.data)
                    if (data.data.code == "1071") {
                        this.dialogVisible = false
                        this.$message({
                            message: '删除消息成功',
                            type: 'success'
                        });
                        this.$router.go(0)
                    } else {
                        this.dialogVisible = false
                        this.$message.error('删除失败');
                    }

                }.bind(this))
            },
            deleteFn2() {

            var that=this;
                this.axios.post("/api/back_class/delete_two", {
                    oneId:that.itemId,
                    twoId:that.id2,
                    enname_one:that.name
                }).then(function(data) {
                    console.log(data.data)
                    if (data.data.code == "1071") {
                        this.dialogVisible2 = false
                        this.$message({
                            message: '删除消息成功',
                            type: 'success'
                        });
                        this.$router.go(0)
                    } else {
                        this.dialogVisible2 = false
                        this.$message.error('删除失败');
                    }

                }.bind(this))
            },

        }



  }
</script>


