<template>
    <div class="conbox">

        <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <h2>一级分类</h2>
            <el-form-item label="中文类名" prop="pass">
                <el-input type="text" v-model="ruleForm2.pass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="英文标示" prop="checkPass">
                <el-input type="text" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
            </el-form-item>
            <h2>二级分类</h2>
            <el-form-item label="中文类名" prop="pass2">
                <el-input type="text" v-model="ruleForm2.pass2" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="英文标示" prop="checkPass2">
                <el-input type="text" v-model="ruleForm2.checkPass2" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm2')" >提交</el-button>
                <el-button @click="resetForm('ruleForm2')">重置</el-button>
            </el-form-item>
        </el-form>


    </div>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('中文类名不能为空'));
        }else if(/^[\u4e00-\u9fa5_a-zA-Z0-9]+$/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的中文类名'));

        }

      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('英文标注不能为空'));
        }else if(/[a-zA-Z]/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的英文标注'));
        }
      };
      return {
        fullscreenLoading:false,
        ruleForm2: {
          pass: '',
          checkPass: '',
          pass2: '',
          checkPass2: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          pass2: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass2: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        var that = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // alert('submit!');
            this.axios.post("api/back_class/add_class_one",{
                enname_one:that.ruleForm2.pass,
                cnname_one:that.ruleForm2.checkPass,
                enname_two:that.ruleForm2.pass2,
                cnname_two:that.ruleForm2.checkPass2
            }).then(function(data){
              console.log(data)
              that.$message({
                message:data.data.msg,
                // type: 'success'
              });
              that.ruleForm2.pass = "";
              that.ruleForm2.pass2 = "";
              that.ruleForm2.checkPass = "";
              that.ruleForm2.checkPass2 = "";
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style scoped>

    h2{
        margin-bottom: 10px;
             color: orange;
             font-size: 16px;
    }
</style>



