<template>
	<el-row class="tac">
  <el-col :span="8">

    <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
      <el-submenu index="1">
        <template slot="title"><i class="el-icon-message"></i>分类管理</template>
          <router-link to='/main/one'><el-menu-item index="1-4-1">添加一级分类</el-menu-item></router-link>
           <router-link to='/main/classList'><el-menu-item index="1-4-1">分类列表</el-menu-item></router-link>
            <router-link to='/main/two'><el-menu-item index="1-4-1">添加二级分类</el-menu-item></router-link>
      </el-submenu>
		 <el-submenu index="2">
        <template slot="title"><i class="el-icon-message"></i>接口操作</template>
           <router-link to='/main/list'><el-menu-item index="1-4-1">添加接口</el-menu-item></router-link>
          <router-link to='/main/update'><el-menu-item index="1-4-1">修改接口</el-menu-item></router-link>
          <router-link to='/main/delate'><el-menu-item index="1-4-1">删除接口</el-menu-item></router-link>
           <router-link to='/main/articleList'><el-menu-item index="1-4-1">接口列表</el-menu-item></router-link>
      </el-submenu>
		 <el-submenu index="3">
        <template slot="title"><i class="el-icon-message"></i>文章管理</template>
           <router-link to='/main/article'><el-menu-item index="1-4-1">添加文章</el-menu-item></router-link>
           <router-link to='/main/artlist'><el-menu-item index="1-4-1">文章列表</el-menu-item></router-link>
      </el-submenu>
    </el-menu>
  </el-col>


</el-row>
</template>
<style>
	.el-menu{
		background: none;
	}
</style>
<script>
  export default {
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>


