import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import main from '@/components/main'
import one from '@/components/page/one.vue'
import list from '@/components/page/list.vue'
import update from '@/components/page/update.vue'
import delate from '@/components/page/delate.vue'
import articleList from '@/components/page/articleList.vue'
import two from '@/components/page/two.vue'
import classList from '@/components/page/classList.vue'
import article from '@/components/page/article.vue'
import amond1 from '@/components/page/amond1.vue'
import amond2 from '@/components/page/amond2.vue'
import artlist from '@/components/page/artlist.vue'
import art_amend from '@/components/page/art_amend.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/main',
      name: 'main',
      component: main,
      children:[
        {
          path:'one',
          name:'one',
          component:one
        },
        {
          path:'list',
          name:'list',
          component:list
        },
        {
          path:'update',
          name:'update',
          component:update
        },
        {
          path:'delate',
          name:'delate',
          component:delate
        },
        {
          path:'articleList',
          name:'articleList',
          component:articleList
        },
        {
          path:'two',
          name:'two',
          component:two
        },
        {
          path:'classList',
          name:'classList',
          component:classList
        },
        {
          path:'article',
          name:'article',
          component:article
        },
        {
          path:'amond1',
          name:'amond1',
          component:amond1
        },
        {
          path:'amond2',
          name:'amond2',
          component:amond2
        },
        {
          path:'artlist',
          name:'artlist',
          component:artlist
        },
        {
          path:'art_amend',
          name:'art_amend',
          component:art_amend
        }
      ]
    }
  ]
})



