<template>
	<div>
		<h2>一级类名修改</h2>
		<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
		  <el-form-item label="中文" prop="pass">
		    <el-input type="text" v-model="ruleForm2.pass" auto-complete="off"></el-input>
		  </el-form-item>
		  <el-form-item label="英文" prop="checkPass">
		    <el-input type="text" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
		  </el-form-item>

		  <el-form-item>
		    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
		    <el-button @click="resetForm('ruleForm2')">重置</el-button>
		  </el-form-item>
</el-form>
	</div>
</template>
<script>
	export default{
		created(){
			console.log(this.$route.params)
		},
		 data() {
      var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('年龄不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入中文'));
        } else if(/^[\u4e00-\u9fa5_a-zA-Z0-9]+$/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的中文类名'));

        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入英文标识'));
        } else if(/[a-zA-Z]/.test(value)){
            callback();
        }else{
            callback(new Error('请输入正确的英文标注'));
        }
      };
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          age: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
      	var that=this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
           	this.axios.post('/api/back_class/amend_class_one',{
           		cnname_one:that.ruleForm2.pass,
           		enname_one:that.ruleForm2.checkPass,
           		oldenname_one:that.$route.params.onedata.enname
           	}).then(function(data){
           		console.log(data.data)
           		if(data.data.code=='1052'){
           			that.$message({
           				message:data.data.msg
           			})
           		}
           	})
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
	}
</script>
<style>
	h2{
		color: orange;
		font-size: 15px;
	}
</style>


