<template>
  <div style="width:700px">
<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <h3>修改文章</h3>
<el-form-item label="文章名" prop="title">
    <el-input type="text" v-model="ruleForm2.article_name" auto-complete="off"></el-input>
</el-form-item>
<el-form-item label="作者" prop="url">
    <el-input v-model="ruleForm2.editer"></el-input>
</el-form-item>


<el-form-item label="内容" prop="sendparams" >
    <el-input
  type="textarea"
  :rows="2"
  placeholder="请输入内容"
  v-model="ruleForm2.content" :autosize='{ minRows: 2, maxRows: 10 }'>
</el-input>
</el-form-item>
<el-form-item label="是否显示" prop="getparams">
	<template>
  <el-radio class="radio" v-model="ruleForm2.art_show" label="1">备选项</el-radio>
  <el-radio class="radio" v-model="ruleForm2.art_show" label="0">备选项</el-radio>
  </template>
</el-form-item>
<el-form-item label="是否推荐" prop="getparams">
	<template>
  <el-radio class="radio" v-model="ruleForm2.recommend" label="1">备选项</el-radio>
  <el-radio class="radio" v-model="ruleForm2.recommend" label="0">备选项</el-radio>
  </template>
</el-form-item>
<el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
    <el-button @click="resetForm('ruleForm2')">重置</el-button>
</el-form-item>
</el-form>
  </div>


</template>

<script>
    export default {
        data() {
            return {
                options_fontorback: [{
                    value: 'back',
                    label: '后台接口'
                }, {
                    value: 'font',
                    label: '前台接口'
                }],
                options_type: [{
                    value: 'get',
                    label: 'get'
                }, {
                    value: 'post',
                    label: 'post'
                }, {
                    value: 'formdata',
                    label: 'formdata'
                }],
                ruleForm2: {
                    article_name: "",
                   editer: "",
                    content: "",
                    art_show: "",
                    recommend: "",
                    daodu: "",
                    time:'',
                    imgsrc:'',
                    id:'',
                    enname_one:'',
                    visitors:''
                },
                rules2: {
                    title: [{
                        required: false,
                        message: '请输入接口名称',
                        trigger: 'blur'
                    }],
                    url: [{
                        required: false,
                        message: '请输入接口地址',
                        trigger: 'blur'
                    }],
                    backorfont: [{
                        required: false,
                        message: '请选择接口归属类型',
                        trigger: 'blur'
                    }],
                    type: [{
                        required: false,
                        message: '请选择接口请求类型',
                        trigger: 'blur'
                    }],
                    sendparams: [{
                        required: false,
                        message: '请输入接口发送参数',
                        trigger: 'blur'
                    }],
                    getparams: [{
                        required: false,
                        message: '请输入接口返回参数',
                        trigger: 'blur'
                    }]
                }
            };
        },
        created() {

            this.ruleForm2 = this.$route.params
            console.log( this.ruleForm2)
        },
        methods: {
            submitForm(formName) {
            	var that=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                    	console.log(that.ruleForm2)
                        this.axios.post("/api/back_article/amendArticle", that.ruleForm2).then(function(data) {

                           console.log(data.data)
                        }.bind(this))

                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            open2() {
                this.$message({
                    message: '修改成功'
                });
            },
            open4() {
                this.$message.error('修改失败');
            }
        }
    }
</script>

<style scoped>
    h3 {
        margin: 20px 0 20px 40px
    }

    .textarea {
        width: 400px;
        height: 500px;
    }
</style>


