<template>
	<div class="main">
			<header>
				当前登录用户: {{head}}
			</header>
			<section>
				<left class='left'></left>
				<div class="middle">
					<router-view></router-view>
				</div>
				<div class="right"></div>
			</section>

		</div>

	</div>
</template>
<style>
	.main{
		width: 100%;
		height: 100%;
		background: url(../../static/img/3.jpg) no-repeat;

	}
	header{
		width: 100%;
		height: 60px;
		border-bottom:1px solid #ccc;
		color: #fff;
		line-height: 60px;
		font-size: 15px;
		padding:0 10px;
		letter-spacing: 2px;
	}
	section{
		width: 100%;
		display: flex;
	}
	.main .left{
		width:200px;

	}
	.el-col{
		width: 200px;
	}

</style>
<script>
	import left from './page/left'
	export default{

		components:{
			left
		},
		created(){
			this.head=sessionStorage.getItem('username')
		}
	}
</script>


